<template>
  <div class="zoehis_date_editor_box" tabindex="-1" @focusin="editorFocusEvn" @focusout="editorFocusoutEvn">
    <div class="zoehis_date_editor_box_inner">
      <div class="zoehis_date_editor_item" v-if="dateobj.year.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_year" check-type="digit" :digit-length="4"
          @keydown.native.tab.prevent
          @keydown.native.left.prevent @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('year')"
          @keydown.native.down.prevent="editorKeyDownEvn('year')"
          @keyup.native="keyupEvn('year')"
          v-model="dateobj.year.value" :clearable="false" :width="60">
        </zoehis-input>
        <span class="zoehis_date_text">年</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.month.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_month" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('month')"
          @keydown.native.down.prevent="editorKeyDownEvn('month')"
          @keyup.native="keyupEvn('month')"
          v-model="dateobj.month.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">月</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.date.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_date" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('date')"
          @keydown.native.down.prevent="editorKeyDownEvn('date')"
          @keyup.native="keyupEvn('date')"
          v-model="dateobj.date.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">日</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.hour.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_hour" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('hour')"
          @keydown.native.down.prevent="editorKeyDownEvn('hour')"
          @keyup.native="keyupEvn('hour')"
          v-model="dateobj.hour.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">时</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.minute.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_minute" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('minute')"
          @keydown.native.down.prevent="editorKeyDownEvn('minute')"
          @keyup.native="keyupEvn('minute')"
          v-model="dateobj.minute.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">分</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.seconds.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_seconds" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('seconds')"
          @keydown.native.down.prevent="editorKeyDownEvn('seconds')"
          @keyup.native="keyupEvn('seconds')"
          v-model="dateobj.seconds.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">秒</span>
      </div>
    </div>
    <div class="zoehis_date_editor_box_btn">
      <span class="zoehis_date_editor_clear" @click="clearEvn">清除</span>
      <zoehis-button size="small" @clickenter="confirmEvn('confirm')">确认</zoehis-button>
    </div>
  </div>
</template>
<script type="text/babel">
  import Popper from '../../../src/utils/vue-popper';
  import { getDayCountOfMonth } from '../../../src/utils/util';
  export default {
    name: 'ZoehisDateEditorBox',
    componentName: 'ZoehisDateEditorBox',
    mixins: [Popper],
    data(){
      return {
        dateobj:{
          year:{value:"2018",visible:true,next:"month",prev:""},
          month:{value:"12",visible:true,next:"date",prev:"year"},
          date:{value:"12",visible:true,next:"hour",prev:"month"},
          hour:{value:"12",visible:true,next:"minute",prev:"date"},
          minute:{value:"12",visible:true,next:"seconds",prev:"hour"},
          seconds:{value:"12",visible:true,next:"",prev:"minute"}
        }
      }
    },
    props:{
      placement: {
        default: 'bottom-start'
      },
      boundariesPadding: {
        default: 0
      },
      popperOptions: {
        default() {
          return {
            forceAbsolute: true,
            gpuAcceleration: false
          };
        }
      },
      preventOverId:{
        type:String,
        default:""
      },
      format: {
        type:String,default:"yyyy-MM-dd HH:mm:ss"
      },
      value: {
        required: true
      },
      initfocus:String,
      endfocus:{type:String,default:"seconds"},//按 enter、tab 时，到哪个输入框就跳出来
      nowtime:Boolean
    },
    methods:{
      //keyup 事件
      keyupEvn(type){
        let keyCode = event.keyCode;
        if(event.ctrlKey && (keyCode==9 || keyCode==13)){
          this.$emit('after-prev');
          return;
        }

        switch (keyCode){
          case 9://tab
            this.tabEvn(type);
            break;
          case 13://enter
            this.enterEvn(type);
            break;
          case 39://right
            this.rightNextEvn(type);
            break;
          case 37://left
            this.leftPrevEvn(type);
            break;
        }
      },
      //设置每一项的属性
      setDateItemAttr(){

        this.dateobj.year.visible = this.format.indexOf("yyyy")!=-1;
        this.dateobj.month.visible = this.format.indexOf("MM")!=-1;
        this.dateobj.date.visible = this.format.indexOf("dd")!=-1;
        this.dateobj.hour.visible = this.format.indexOf("HH")!=-1;
        this.dateobj.minute.visible = this.format.indexOf("mm")!=-1;
        this.dateobj.seconds.visible = this.format.indexOf("ss")!=-1;
      },
      //确认按钮事件
      confirmEvn(type='confirm'){
        let curDate = new Date();
        let year = curDate.getFullYear(),month = curDate.getMonth()+1,date = curDate.getDate(),
          hour = curDate.getHours(),minute = curDate.getMinutes(),seconds = curDate.getSeconds();
        //处理年
        if((""+this.dateobj.year.value).length>2){
          year = this.dateobj.year.value;
        }
        this.dateobj.year.value = ""+year;

        //处理月
        if(this.dateobj.month.value){
          month = parseInt(this.dateobj.month.value)%12 || 12;
        }
        this.dateobj.month.value = ""+month;

        //处理日
        if(this.dateobj.date.value){
          let dayCount = getDayCountOfMonth(parseInt(year),parseInt(month)-1);//根据年月 获取 一个月共有多少天
          date = parseInt(this.dateobj.date.value)%dayCount || dayCount;
        }
        this.dateobj.date.value = ""+date;

        //处理小时
        if(this.dateobj.hour.value){
          hour = parseInt(this.dateobj.hour.value)%24;
        }
        this.dateobj.hour.value = ""+hour;
        //处理分钟
        if(this.dateobj.minute.value){
          minute = parseInt(this.dateobj.minute.value)%60;
        }
        this.dateobj.minute.value = ""+minute;
        //处理秒
        if(this.dateobj.seconds.value){
          seconds = parseInt(this.dateobj.seconds.value)%60;
        }
        this.dateobj.seconds.value = ""+seconds;


        let dateStr = (this.dateobj.year.value) + "-" + (this.dateobj.month.value) +"-"+
          (this.dateobj.date.value)+ " " +(this.dateobj.hour.value) + ":" +
          (this.dateobj.minute.value)+ ":" +(this.dateobj.seconds.value);
        this.$emit("save",new Date(dateStr),type);
      },
      clearEvn(){
        this.$emit("save","","clear");
      },

      //keydown事件 up
      editorKeyUpEvn(type){
        switch (type){
          case "year"://年
            let year = parseInt(this.dateobj.year.value||0);
            if(year>=9999){
              this.dateobj.year.value = "9999";
            }else {
              this.dateobj.year.value = ""+(++year);
            }
            break;
          case "month"://月
            let month = parseInt(this.dateobj.month.value||0);
            ++month;
            if(month<1 || month>12){
              month="01";
            }else if(month<10){
              month = "0" + month;
            }
            this.dateobj.month.value = ""+month;
            break;
          case "date"://天
            let date = parseInt(this.dateobj.date.value||0);
            ++date;
            if(date<1 || date>31){
              date="01";
            }else if(date<10){
              date = "0" + date;
            }
            this.dateobj.date.value = ""+date;
            break;

          case "hour"://时
            let hour = parseInt(this.dateobj.hour.value||0);
            ++hour;
            if(hour<1 || hour>23){
              hour="00";
            }else if(hour<10){
              hour = "0" + hour;
            }
            this.dateobj.hour.value = ""+hour;
            break;
          case "minute"://分
            let minute = parseInt(this.dateobj.minute.value||0);
            ++minute;
            if(minute<1 || minute>59){
              minute="00";
            }else if(minute<10){
              minute = "0" + minute;
            }
            this.dateobj.minute.value = ""+minute;
            break;
          case "seconds"://秒
            let seconds = parseInt(this.dateobj.seconds.value||0);
            ++seconds;
            if(seconds<1 || seconds>59){
              seconds="00";
            }else if(seconds<10){
              seconds = "0" + seconds;
            }
            this.dateobj.seconds.value = ""+seconds;
            break;
        }
        this.selectInput(type);
      },
      //keydown事件 down
      editorKeyDownEvn(type){
        switch (type){
          case "year"://年
            let year = parseInt(this.dateobj.year.value||0);
            if(year<=100){
              this.dateobj.year.value = "100";
            }else {
              this.dateobj.year.value = ""+(--year);
            }
            break;
          case "month"://月
            let month = parseInt(this.dateobj.month.value||0);
            --month;
            if(month<1 || month>12){
              month="12";
            }else if(month<10){
              month = "0" + month;
            }
            this.dateobj.month.value = ""+month;
            break;
          case "date"://天
            let date = parseInt(this.dateobj.date.value||0);
            --date;
            if(date<1 || date>31){
              date="31";
            }else if(date<10){
              date = "0" + date;
            }
            this.dateobj.date.value = ""+date;
            break;
          case "hour"://时
            let hour = parseInt(this.dateobj.hour.value||0);
            --hour;
            if(hour<0 || hour>23){
              hour="23";
            }else if(hour<10){
              hour = "0" + hour;
            }
            this.dateobj.hour.value = ""+hour;
            break;
          case "minute"://分
            let minute = parseInt(this.dateobj.minute.value||0);
            --minute;
            if(minute<0 || minute>59){
              minute="59";
            }else if(minute<10){
              minute = "0" + minute;
            }
            this.dateobj.minute.value = ""+minute;
            break;
          case "seconds"://秒
            let seconds = parseInt(this.dateobj.seconds.value||0);
            --seconds;
            if(seconds<0 || seconds>59){
              seconds="59";
            }else if(seconds<10){
              seconds = "0" + seconds;
            }
            this.dateobj.seconds.value = ""+seconds;
            break;
        }
        this.selectInput(type);
      },
      //全选某个输入框
      //type:类型
      selectInput(type="year"){

        this.$nextTick(()=>{

          let curVm = this.findTheRefVm(type);
          if(!curVm) return;
          curVm.inputSelect();
        });
      },
      //找到根据type 找到 对应的vm对象，当前找不到 则找下一个
      findTheRefVm(type){
        if(!type) return;
        let curObj = this.dateobj[type];
        if(!curObj) return;
        if(curObj.visible && this.$refs[`editor_${type}`]) return this.$refs[`editor_${type}`];
        return this.findTheRefVm(curObj.next);
      },
      editorFocusEvn(){
        if(this.blurtimer){
          clearTimeout(this.blurtimer);
          this.blurtimer=null;
        }
      },
      editorFocusoutEvn(){
        if(this.blurtimer){
          clearTimeout(this.blurtimer);
        }
        this.blurtimer=setTimeout(()=>{
          this.$emit("box-blur");
        },20);
      },
      //tab 事件
      tabEvn(type){
        let curVm = this.findTheRefVm(this.dateobj[type].next);
        if(type==this.endfocus || !curVm){
          this.$emit("tab-end");
          return;
        }
        curVm.inputSelect();
      },
      //按左键 事件
      leftPrevEvn(type){
        let curVm = this.findTheRefVm(this.dateobj[type].prev);
        if(!curVm){
          this.selectInput(type);
          return;
        }
        curVm.inputSelect();
      },
      //按右键 事件
      rightNextEvn(type){
        let curVm = this.findTheRefVm(this.dateobj[type].next);
        if(!curVm){
          this.selectInput(type);
          return;
        }
        curVm.inputSelect();
      },
      //enter 事件
      enterEvn(type){
        let curVm = this.findTheRefVm(this.dateobj[type].next);
        if(type==this.endfocus || !curVm){
          this.confirmEvn("enter");
          return;
        }
        curVm.inputSelect();
      },

    },
    computed:{

    },
    watch:{
      //监听v-model传进来的值
      value:{
        immediate:true,//第一次的时候会立即执行，而不是value变化的时候才执行
        handler(val){
          if(this.nowtime && !val) val=new Date();//是否要初始化为当前时间
          if(!val){
            this.dateobj.year.value = "";
            this.dateobj.month.value = "";
            this.dateobj.date.value = "";
            this.dateobj.hour.value = "";
            this.dateobj.minute.value = "";
            this.dateobj.seconds.value = "";
          }else {
            let year = val.getFullYear();
            let month = String(1+val.getMonth()).length<2 ? "0"+String(val.getMonth()+1) : val.getMonth()+1;
            let date = (""+val.getDate()).length<2 ? ("0"+val.getDate()) : val.getDate();
            let hour = (""+val.getHours()).length<2 ? ("0"+val.getHours()) : val.getHours();
            let minute = (""+val.getMinutes()).length<2 ? ("0"+val.getMinutes()) : val.getMinutes();
            let seconds = (""+val.getSeconds()).length<2 ? ("0"+val.getSeconds()) : val.getSeconds();

            this.dateobj.year.value = ""+year;
            this.dateobj.month.value = ""+month;
            this.dateobj.date.value = ""+date;
            this.dateobj.hour.value = ""+hour;
            this.dateobj.minute.value = ""+minute;
            this.dateobj.seconds.value = ""+seconds;
          }
        }
      },
    },
    created(){

    },
    mounted() {
      this.setDateItemAttr();
      this.referenceElm = this.$parent.$refs.reference.$el; //父组件的input输入框的dom元素
      this.$parent.popperElm = this.popperElm = this.$el;
      this.$on('updatePopper', () => {
        if (this.$parent.visible) this.updatePopper();
      });
    },
  };
</script>



